@import"https://fonts.googleapis.com/css2?family=Open+Sans:wght@500;700&display=swap";body{font-family:"Open Sans",sans-serif;color:#fff;background-color:#1c531d;display:flex;justify-content:center;align-items:center;min-height:100vh;margin:0}.game-container{text-align:center;max-width:600px;padding:20px;border-radius:8px;background-color:#1c531d}.game-container h1{font-weight:bold;margin-bottom:10px}.game-container p{font-weight:500}.card-grid{display:grid;grid-template-columns:repeat(3, 1fr);gap:20px;margin:20px 0}.card{width:100px;height:150px;perspective:1000px;position:relative;cursor:pointer}.card.flipped .card-front{transform:rotateY(0deg)}.card.flipped .card-back{transform:rotateY(180deg)}.card .card-front,.card .card-back{position:absolute;width:100%;height:100%;border-radius:8px;backface-visibility:hidden;transition:transform .6s ease-in-out;display:flex;align-items:center;justify-content:center;font-size:2rem;font-weight:bold}.card .card-front{background-color:#fff;color:#1c531d;transform:rotateY(180deg)}.card .card-back{background-image:url("../src/assets/Card-back.png");background-size:cover;transform:rotateY(0deg)}.status{display:flex;flex-direction:column;align-items:center;margin-top:20px}.status p{font-size:1.2rem}.status #restartButton{background-color:#238ab6;color:#fff;border:none;padding:10px 20px;font-size:1rem;font-weight:bold;border-radius:5px;cursor:pointer;transition:background-color .3s ease}.status #restartButton:hover{background-color:rgb(26.7741935484,105.5668202765,139.2258064516)}
